import React from "react";
import PropTypes from "prop-types";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { withStyles } from "@material-ui/core/styles";
import { styles } from "./styles";

const uuidv4 = require("uuid/v4");

const Banners = ({ classes, settings, isSlider, dfp }) => {
    return (
        <div className={classes.root}>
            {isSlider && (
                <div className={classes.fullWidth}>
                    <Slider {...settings}>
                        {dfp.map(item => (
                            <div key={uuidv4()} className={classes.item}>
                                <div id={item.tag} />
                            </div>
                        ))}
                    </Slider>
                </div>
            )}
            {!isSlider &&
        dfp.map(item => (
            <div id={item.tag} key={uuidv4()} className={classes.notSlider} />
        ))}
        </div>
    );
};

Banners.propTypes = {
    classes: PropTypes.object.isRequired,
    settings: PropTypes.object,
    isSlider: PropTypes.bool,
    dfp: PropTypes.arrayOf(PropTypes.object),
};

Banners.defaultProps = { infinite: false };

export default withStyles(styles, { withTheme: true })(Banners);
